<template>
  <div class="title">
    <span class="title-text">{{ title || 'Dashboard' }}</span>
  </div>
</template>

<script>
  export default {
    data: () => ({
      title: '',
    }),
    watch: {
      '$route': {
        immediate: true,
        deep: true,
        handler(val) {
          if (val.name) {
            this.title = val.name;
          }
        }
      }
    },
  }
</script>

<style lang="scss" scoped>
.title {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 24px;
  width: 100%;
  flex: 1;
  color: black;
  font-weight: 500;

&-text {
   padding-left: 50px;
 }
}

.el-main .title {
  display: flex; // none
  margin-top: -25px;
  margin-bottom: 25px;
  background-color: white;
  height: 83px;
}

