<template>
  <div v-if="currentCampaign" class="nav-container-dark campaign-special">
    <DeleteModal
        v-model="deletePromptVisible"
        :step-id="deleteId"
        :step-name="deleteName"
        @closed="deletePromptVisible = false"/>
    <div class="controls">
      <el-select
          v-model="value"
          placeholder="Select"
          popper-class="campaign-select"
          class="campaign-select"
          @change="setCurrentCampaign">
        <el-option
            v-for="c in campaigns"
            :key="c.id"
            :label="c.name"
            :value="c.id">
        </el-option>
      </el-select>
      <div class="buttons">
        <el-button type="primary" id="new-page-button" @click="handleNewStep">
          New Page
        </el-button>
        <el-upload
            action=""
            :auto-upload="false"
            :show-file-list="false"
            :on-change="onChange"
            :limit="1"
            accept=".zip">
          <el-button type="info" id="import-button">
            Import Template
          </el-button>
          <div class="error" v-if="importFail">
            ZIP file is not valid
          </div>
        </el-upload>
      </div>
    </div>

    <div v-for="(s, index) in currentCampaign.settings.steps" :key="index" class="campaign"
         @click="handleStepChange(index)"
         :class="{'active': index == currentCampaignPage }">
        <span class="campaign-name" :title="s.name">
          {{ s.name }}
        </span>
      <span class="campaign-icons">
          <el-button circle class="copy" @click.stop="handleCopyStepByIndex(index)">
              <ii-copy/>
          </el-button>
        <el-button circle class="delete" @click.stop="handleDeleteStepByIndex(index, s.name)"
                   v-if="currentCampaign.settings.steps.length > 1">
              <ii-delete/>
        </el-button>
      </span>
    </div>
  </div>
</template>

<script>
import {mapActions, mapGetters} from "vuex";
import DeleteModal from "@/components/DeleteModal";

export default {
  name: "CampaignSpecialSidebar",
  components: {DeleteModal},
  data: () => ({
    value: null,
    importFail: false,
    deletePromptVisible: false,
    deleteId: '',
    deleteName: '',
  }),
  computed: {
    ...mapGetters(['currentCampaign', 'currentCampaignPage', 'campaigns']),
  },
  watch: {
    currentCampaign: {
      handler(v) {
        if (v) {
          this.value = v.id;
        }
      },
      deep: true,
      immediate: true,
    },
  },
  methods: {
    ...mapActions([
        'setCurrentStep', 'addStep', 'deleteStep', 'setCurrentCampaign', 'importStep', 'duplicateStep'
    ]),
    handleNewStep() {
      this.addStep()
    },
    handleDeleteStepByIndex(index, name) {
      this.deleteId = index
      this.deleteName = name
      this.deletePromptVisible = true
    },
    handleCopyStepByIndex(index) {
      if(!this.currentCampaign.settings.steps[index].page) {
        this.handleNewStep()
      } else {
        this.duplicateStep(index)
      }
    },
    handleStepChange(index) {
      this.setCurrentStep(index)
    },
    onChange(file, list) {
      this.importFail = false
      this.importStep(file.raw).then((res) => {
        list.length = 0
        if (res.error) {
          this.importFail = true
        }
      })
    },
  }
}
</script>

<style lang="scss">
.campaign-special {

  .el-upload {
    width: 100%;
  }

  .el-select {
    width: 100%;

    .el-input {

      &__inner {
        height: 33px;
      }
    }
  }
}
</style>

<style lang="scss" scoped>

.sidebar {
  position: relative;
  width: 320px;
  height: 100%;
  display: flex;
  transition: all 0.4s ease, height 0s ease;
  z-index: 1;

  .controls {
    padding: 15px 25px;
  }

  .error {
    color: #f0c600;
  }

  &-wrapper {
    width: 320px;
    display: flex;
  }

  .el-button {
    width: 100%;
    margin-left: 0;

    &#new-page-button {
      background-color: #B8C2D8;

      &:hover {
        background-color: $clr-btn;
      }
    }

    &#import-button {
      background-color: #B8C2D8;

      &:hover {
        background-color: $clr-green;
      }
    }
  }
}

.campaign {
  height: 45px;
  background-color: white;
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  cursor: pointer;
  color: black;
  transition: all ease 0.3s;

  &.active, &:hover {
    background-color: #EFF2F9;
  }

  &-name {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }

  &-icons {
    min-width: 70px;
    display: flex;
    justify-content: space-evenly;

    .el-button {
      width: 30px;
      height: 30px;
      background-color: transparent;
      color: $clr-txt-nav;
      padding: 0;

      &:hover {
        color: black;
        transform: rotate(12deg);
      }

      .svg {
        width: 100%;
        height: 100%;
        margin: 0;
      }
    }
  }

}

.buttons {
  margin-top: 25px;

  .el-button {
    margin-bottom: 15px;
  }
}


.el-menu {
  width: 265px;
  display: flex;
  flex-direction: column;
  border: none;

  &::before, &::after {
    display: none;
  }

  &-item {
    height: 34px;
    margin-bottom: 6px;
    padding-left: 40px !important;
    padding-right: 0;
    color: $clr-txt-nav;
    line-height: 34px;
    background-color: transparent !important;
    transition: all 1s ease;

    &:hover {
      color: white;
    }

    &:active, &.is-active {
      color: $clr-primary;
    }

    span {
      transition: opacity 1s ease;
      opacity: 1;
    }
  }

  .nav-container {
    background-color: white;
    padding-bottom: 52px;
    font-weight: 700;
    transition: all 1s ease;

    &-dark {
      transition: all 1s ease;
      background-color: white;
      flex-grow: 1;
    }
  }

  .nav-section {
    color: white;
    font-weight: 500;
    transition: all 1s ease;
    height: 17px;
    opacity: 1;
  }
}

.collapsed {
  width: 36px;

  .nav-section {
    overflow: hidden;
    opacity: 0;
    height: 0;
    margin-top: 0;
    margin-bottom: 0;
    //transform: translate(-70px, -15px);
  }

  .nav-container, .nav-container-dark {
    padding: 23px 8px;
    background-color: white;
  }

  .el-menu-item {
    padding: 0 !important;

    svg {
      transition: all ease 0.2s;

      &:hover {
        transform: scale(1.06) rotate(10deg);
      }
    }

    span {
      opacity: 0;
    }
  }
}
</style>
