<template>
  <div class="sidebar-wrapper" :class="{mobileShow, specialSidebar, wide: isCollapsed}">
    <div class="opacity" @click="mobileShow = false"/>
    <div class="sidebar" :class="{wide: isCollapsed}">
      <el-menu router class="second" :class="{wide: isCollapsed}">
        <component :is="sidebarC"/>
      </el-menu>
      <el-menu router class="first" :class="{collapsed: isCollapsed}">
        <div class="nav-container" :class="{collapsed: isCollapsed}">
          <router-link to="/" id="home">
            <div class="logo" :class="{collapsed: isCollapsed}">
              <ii-logo-img class="logo-img"/>
              <ii-logo-text class="logo-text"/>
            </div>
          </router-link>
          <Modal v-model="dialogVisible"/>
          <el-menu-item @click="dialogVisible = true">
            <ii-create/>
            <span>Create</span>
          </el-menu-item>
          <router-link to="/campaign" class-active="active">
            <!-- :class="{'active': true }" -->
            <el-menu-item>
              <ii-campaigns/>
              <span>Campaigns</span>
            </el-menu-item>
          </router-link>
          <router-link to="/settings" class-active="active">
            <el-menu-item>
              <ii-settings/>
              <span>Settings</span>
            </el-menu-item>
          </router-link>
          <router-link to="/contest" class-active="active">
            <el-menu-item>
              <ii-contest/>
              <span>Contests</span>
            </el-menu-item>
          </router-link>
          <h3 class="nav-section"/>
          <a href="http://training.iglooapp.com/" target="_blank">
            <el-menu-item>
              <ii-tutorial/>
              <span>Tutorials</span>
            </el-menu-item>
          </a>
          <a href="http://sambakkersupport.com/" target="_blank">
            <el-menu-item>
              <ii-support/>
              <span>Support</span>
            </el-menu-item>
          </a>
        </div>
        <div class="upgrades" :class="{collapsed: isCollapsed}">
          <div class="upgrade upgrade-text title">Upgrades</div>
          <div class="upgrade">
            <ii-all-access/>
            <span class="upgrade-text">
              All Access Pass
            </span>
          </div>
          <div class="upgrade">
            <ii-domains/>
            <span class="upgrade-text">
              + 20 Domains
            </span>
          </div>
          <div class="upgrade">
            <ii-bonuses/>
            <span class="upgrade-text">
              Bonuses
            </span>
          </div>
        </div>
      </el-menu>
    </div>
  </div>
</template>

<script>
import Modal from "@/components/Modal";
import CampaignSpecialSidebar from "@/components/CampaignSpecialSidebar";

export default {
  components: {
    Modal,
  },
  data: () => ({
    specialSidebar: false,
    dialogVisible: false,
    isCollapsed: false,
    mobileShow: false,
    sidebarC: CampaignSpecialSidebar,
  }),
  watch: {
    '$route': {
      immediate: true,
      deep: true,
      handler(val) {
        this.isCollapsed = !!val.meta.sidebar;
        this.specialSidebar = !!val.meta.specialSidebar;

        if (typeof (val.meta.sidebar) != "boolean") {
          this.sidebarC = val.meta.sidebar;
        } else {
          this.sidebarC = CampaignSpecialSidebar;
        }
      }
    }
  },
  methods: {
    openSidebar() {
      this.mobileShow = !this.mobileShow;
    }
  },
  created() {
    this.$bus.$on('open-sidebar', this.openSidebar);
  }
}
</script>

<style lang="scss">
//.router-link-exact-active  {
//  background: red !important;
//  color: white !important;
//}
//.active {
//
//}

.campaign-select {
  .el-input {

    &__inner {

    }

    .el-input__icon {
      line-height: 33px !important;
    }
  }
}
</style>

<style lang="scss" scoped>
.opacity {
  position: absolute;
  top: 80px;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: black;
  opacity: 0;
  pointer-events: none;
  z-index: 1;
  transition: opacity 0.4s;
}

.sidebar {
  position: relative;
  width: 265px;
  height: 100%;
  display: flex;
  transition: all 0.4s ease, height 0s ease;
  z-index: 1;
  box-shadow: 0px 0px 24px 0px rgba(109, 161, 208, 0.13);
  font-family: 'Inter', sans-serif;
  font-weight: 400 !important;

  &.wide {
    width: 320px;

    @media (max-width: $media-tablet) {

    }
  }

  .nav-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 12px;


    border-right: solid 1px #EFF2F9;

    .logo {
      display: flex;
      align-items: flex-end;
      gap: 10px;

      padding: 20px 0;
      width: 220px;

      overflow: hidden;
      justify-content: flex-start;
      margin-left: 44px;

      height: 80px;

      transition: all ease 1s;

      &-img {
        min-height: 26px;
        min-width: 48px;
      }

      &-text {
        min-height: 23px;
        min-width: 73px;
        margin-left: 0px;
        opacity: 1;
      }

      &.collapsed {
        margin-left: 13px;

        .logo-text {
          transition: all ease 1s;
          margin-left: 50px;
          opacity: 0;
        }
      }

      svg {

      }
    }
  }


  &-wrapper {
    width: 220px;
    display: flex;

    &.wide {
      width: 320px;
    }
  }

  .el-menu {
    overflow-y: auto;
  }
}

.campaign {
  height: 57px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;

  cursor: pointer;
  color: $clr-txt-nav;
  transition: all ease 0.3s;
}

// upgrades
.first {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 265px;
  height: 100%;
  transition: all 1s ease;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  .upgrades {
    width: 186px;
    height: 248px;
    min-height: 248px;
    background: url("~@/assets/upg-gradient.svg");
    background-repeat: no-repeat; // round
    background-size: cover;
    //background-position: center;
    //background-blend-mode: inherit;
    border-radius: 8px;
    margin-bottom: 16px;
    align-self: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    transition: all ease 1s;

    font-size: 14px;

    padding: 66px 0px 0px;

    .upgrade {
      // TODO refactor
      height: 100%;
      width: 100%;
      color: white;
      display: flex;
      align-items: center;
      overflow: hidden;
      transition: all ease 1s;
      position: relative;
      padding: 0 0 0 25px;

      left: 0px;

      // TODO ?
      &:last-child {
        padding-bottom: 10px;
      }

      &-text {
        opacity: 1;
        white-space: nowrap;
        position: absolute;
        left: 56px;
        transition: all ease 1s;
      }

      &.title {
        height: auto;
        font-size: 27px;
        font-weight: 700;
        text-align: center;
        left: 0px;
        top: 18px;
        transition: all ease 0.8s !important;
      }
    }

    &.collapsed {
      padding: 0; // 40px 0;
      margin-bottom: 0;
      border-radius: 0;

      svg {
        margin-right: 0;
      }


      .upgrade {
        //justify-content: center;

        left: 0px;

        &:last-child {
          padding-bottom: 0;
        }

        &-text {
          opacity: 0;
          position: absolute;
        }

        &.title {
          opacity: 0;
          left: -120px;
        }
      }
    }
  }
}

.el-menu {
  width: 220px;
  display: flex;
  flex-direction: column;
  border: none;

  &::before, &::after {
    display: none;
  }

  a {
    text-decoration: none !important;
    text-underline: none !important;
  }

  &-item {
    height: 42px;
    width: 190px;

    margin-left: 20px;
    margin-bottom: 0px;

    border-radius: 99px;

    padding: 4px 20px;

    color: black;
    line-height: 34px;
    //background-color: transparent !important;
    transition: all 1s ease;

    span {
      transition: opacity 1s ease;
      opacity: 1;
    }
  }

  &.collapsed {
    .el-menu-item {
      width: 60px !important;
      padding-left: 16px !important;
      padding-right: 16px !important;
      //transition: all 0.6s ease;
    }
  }

  svg {
    margin-right: 10px;
    height: 26px;
    width: 26px;
  }

  .nav-container {
    background-color: white;
    padding-bottom: 52px;
    //font-weight: 700;
    transition: all 1s ease;
    width: 320px;

    &-dark {
      transition: all 1s ease;
      background-color: white;
      flex-grow: 1;
    }
  }

  .nav-section {
    color: black;
    font-weight: 500;
    transition: all 1s ease;
    height: 17px;
    opacity: 1;
  }
}

.second {
  padding-left: 74px;
  background-color: white;
  transition: all 0.4s ease;

  &.wide {
    width: 320px !important;
  }

  .nav-container, .nav-container-dark {
    background: white;
  }
}

.collapsed {
  width: 74px !important;

  .nav-section {
    overflow: hidden;
    opacity: 0;
    //height: 0;
    //margin-top: 0;
    //margin-bottom: 0;
    //transform: translate(-30px, -15px);
  }

  .nav-container, .nav-container-dark {
    //padding: 23px 0px;
    background-color: white;
  }

  .el-menu-item {
    margin-left: 8px !important;

    span {
      opacity: 0;
    }
  }
}

@media (max-width: $media-tablet) {
  .specialSidebar {
    .sidebar {
      left: -320px;
      width: 0px !important;
    }

    &.sidebar-wrapper {
      width: 0px !important;
    }

    &.mobileShow {
      .sidebar {
        position: absolute;
        top: 0px;
        left: 0;
        width: 265px;
        bottom: 0;
        height: unset;

        &.wide {
          width: 320px !important;
        }
      }

      .opacity {
        opacity: 0.8;
        pointer-events: auto;
      }
    }
  }
}

@media (max-width: $media-mobile) {
  .sidebar {
    left: -320px;
    width: 0px !important;
  }

  .sidebar-wrapper {
    width: 0px !important;
  }

  .mobileShow {
    .sidebar {
      position: absolute;
      top: 0px;
      left: 0;
      width: 265px;
      bottom: 0;
      height: unset;

      &.wide {
        width: 320px !important;
      }
    }

    .opacity {
      opacity: 0.8;
      pointer-events: auto;
    }
  }
}
</style>

<style lang="scss">
.el-menu-item {
  transition: all .3s linear !important;

  svg {
    * {
      transition: transform .5s cubic-bezier(0.34, 1.56, 0.64, 1);
      transform-origin: 13px 13px;
    }

    .blue {
      fill: black;
    }

    .white {
      fill: white;
      transition: all .3s linear;
    }
  }

  &:hover {
    background: #F5FAFE;
    color: #0EA9EE;

    svg {
      .blue {
        fill: #0EA9EE; // TODO ?
      }

      .white {
        fill: #F5FAFE;
      }
    }
  }
}

.router-link-exact-active, .active {
  .el-menu-item {
    //background: transparent;

    background: black;
    color: white;

    &:focus {
      background: black;
      color: white;
    }

    svg {
      .blue {
        fill: white;
      }

      .white {
        fill: black;
      }
    }

    &:hover {
      background: black;
      color: white;

      svg {
        .blue {
          fill: white;
        }

        .white {
          fill: black;
        }
      }
    }
  }
}

.upgrade {

  &:hover {
    background: linear-gradient(90deg, rgba(255, 255, 255, 0.22) 0%, rgba(255, 255, 255, 0) 100%);
    cursor: pointer;
  }
  
  &.title:hover {
    background: none;
    cursor: unset;
  }

   svg {

     * {
       transition: transform .5s cubic-bezier(0.34, 1.56, 0.64, 1);
       transform-origin: 13px 13px;
     }

     .blue {
       fill: white;
     }

     .white {
       fill: red;
       transition: all .3s linear;

     }
   }
}


</style>
