<template>
  <div v-if="contests" class="nav-container-dark contest-special">
    <ContestModal v-model="dialogVisible"/>
    <div class="controls">
      <el-select
          v-model="value"
          placeholder="Select"
          popper-class="contest-select"
          class="contest-select"
          @change="setCurrentContest">
        <el-option
            v-for="c in contests"
            :key="c.id"
            :label="c.name"
            :value="c.id">
        </el-option>
      </el-select>
      <div class="buttons">
        <el-button type="info" id="import-button" @click="handleNewContest">
          Create New Contest
        </el-button>
      </div>
    </div>
    <div v-for="c in contests" :key="c.id" class="contest"
         @click="setCurrentContest(c.id)"
         :class="{'active': c.id == currentContest.id }">
        <span class="contest-name" :title="c.name">
          {{ c.name }}
        </span>
      <span class="contest-icons">
          <el-button circle class="copy" @click.stop="handleDuplicateContest(c.id)">
              <ii-copy/>
          </el-button>
        <el-button circle class="delete" @click.stop="handleDeleteContest(c.id)">
              <ii-delete/>
        </el-button>
      </span>
    </div>

  </div>
</template>

<script>
import {mapActions, mapGetters} from "vuex";
import ContestModal from "@/components/ContestModal";

export default {
  name: "ContestSpecialSidebar",
  components: {
    ContestModal
  },
  data: () => ({
    value: null,
    dialogVisible: false,
  }),
  computed: {
    ...mapGetters(['currentContest', 'contests']),
  },
  watch: {
    currentContest: {
      handler(v) {
        if (v) {
          this.value = v.name;
        }
      },
      deep: true,
      immediate: true,
    },
  },
  methods: {
    ...mapActions(['setCurrentContest', 'createContest', 'deleteContest', 'duplicateContest']),
    handleNewContest() {
      this.dialogVisible = true;
    },
    handleDeleteContest(id) {
      this.deleteContest(id)
    },
    handleDuplicateContest(id) {
      this.duplicateContest(id)
    },
  }
}
</script>

<style lang="scss">
.contest-select {
  .el-select {
    width: 100%;

    .el-input {

      &__inner {
        height: 33px;
      }
    }
  }
}
</style>

<style lang="scss" scoped>

.sidebar {
  position: relative;
  width: 320px;
  height: 100%;
  display: flex;
  transition: all 0.4s ease, height 0s ease;
  z-index: 1;

  .controls {
    padding: 15px 25px;
  }

  .error {
    color: #f0c600;
  }

  &-wrapper {
    width: 320px;
    display: flex;
  }

  .el-button {
    width: 100%;
    margin-left: 0;

    &#new-page-button {
      background-color: #B8C2D8;

      &:hover {
        background-color: $clr-btn-primary;
      }
    }

    &#import-button {
      background-color: #B8C2D8;

      &:hover {
        background-color: $clr-green;
      }
    }
  }
}

.contest {
  height: 45px;
  background-color: white;
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  cursor: pointer;
  color: black;
  transition: all ease 0.3s;

  &.active, &:hover {
    background-color: #EFF2F9;
  }

  &-name {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }

  &-icons {
    min-width: 70px;
    display: flex;
    justify-content: space-evenly;

    .el-button {
      width: 30px;
      height: 30px;
      background-color: transparent;
      color: $clr-txt-nav;
      padding: 0;

      &:hover {
        color: black;
        transform: rotate(12deg);
      }

      .svg {
        width: 100%;
        height: 100%;
        margin: 0;
      }
    }
  }
}

.buttons {
  padding: 0 12px;
  margin-top: 25px;

  .el-button {
    margin-bottom: 15px;
    color: white !important;
  }
}

.el-select {
  width: 100%;

  .el-input {
    &__inner {
      height: 70px;
    }
  }
}

.el-menu {
  width: 265px;
  display: flex;
  flex-direction: column;
  border: none;

  &::before, &::after {
    display: none;
  }

  &-item {
    height: 34px;
    margin-bottom: 6px;
    padding-left: 40px !important;
    padding-right: 0;
    color: $clr-txt-nav;
    line-height: 34px;
    background-color: transparent !important;
    transition: all 1s ease;

    &:hover {
      color: white;
    }

    &:active, &.is-active {
      color: $clr-primary;
    }

    span {
      transition: opacity 1s ease;
      opacity: 1;
    }
  }

  .nav-container {
    background-color: white;
    padding-bottom: 52px;
    font-weight: 700;
    transition: all 1s ease;

    &-dark {
      transition: all 1s ease;
      background-color: white;
      flex-grow: 1;
    }
  }

  .nav-section {
    color: white;
    font-weight: 500;
    transition: all 1s ease;
    height: 17px;
    opacity: 1;
  }
}

.collapsed {
  width: 36px;

  .nav-section {
    overflow: hidden;
    opacity: 0;
    height: 0;
    margin-top: 0;
    margin-bottom: 0;
    //transform: translate(-70px, -15px);
  }

  .nav-container, .nav-container-dark {
    padding: 23px 8px;
    background-color: white;
  }

  .el-menu-item {
    padding: 0 !important;

    svg {
      transition: all ease 0.2s;

      &:hover {
        transform: scale(1.06) rotate(10deg);
      }
    }

    span {
      opacity: 0;
    }
  }
}
</style>
