<template>
  <div class="login" v-if="!reset">
    <div class="login-form">
      <div class="logo">
        <ii-logo-img class="logo-img"/>
        <ii-logo-text class="logo-text"/>
      </div>
<!--      <div class="err" v-if="loginFail">-->
<!--        Username or password are incorrect.-->
<!--      </div>-->
<!--      <div class="info" v-else>-->
<!--        LOGIN TO YOUR ACCOUNT BELOW-->
<!--      </div>-->
      <div class="inputs">
        <el-input
            v-model="email"
            placeholder="Email"
            name="email"
            class="input"
            @keyup.enter.native="handleLogin"/>
        <el-input
            v-model="password"
            placeholder="Password"
            name="password"
            type="password"
            class="input"
            @keyup.enter.native="handleLogin"/>
      </div>
      <el-button :disabled="loading || password.length == 0 || email.length == 0" v-loading="loading" type="info" id="login" @click="handleLogin">
        Login to iGloo
      </el-button>
      <div class="forget" @click="reset = !reset">
        <ii-lock/>
        Forgot your password?
      </div>
    </div>
  </div>
  <div class="login" v-else>
    <div class="login-form">
      <div class="logo">
        <ii-logo-img class="logo-img"/>
        <ii-logo-text class="logo-text"/>
      </div>
<!--      <div class="info" v-if="!resetSuccess">LOGIN TO YOUR ACCOUNT BELOW</div>-->
      <div class="inputs" v-if="!resetSuccess">
        <el-input
            v-model="email"
            placeholder="Email"
            name="email"
            class="input"
            @keyup.enter.native="handleLogin"/>
      </div>
      <el-button :disabled="loading" v-loading="loading" type="info" id="instructions" @click="handleReset"
                 v-if="!resetSuccess">
        Send instructions
      </el-button>
      <div class="info" v-else>The instructions have been sent to the provided email</div>
      <div class="forget" @click="reset = !reset">
        <ii-lock/>
        Back to login
      </div>
    </div>
  </div>
</template>

<script>
import {mapActions} from "vuex";

export default {
  name: "Login",
  data: () => ({
    email: '',
    password: '',
    loading: false,
    reset: false,
    resetSuccess: false,
    loginFail: false
  }),
  methods: {
    ...mapActions(['login', 'resetPassword']),
    handleLogin() {
      this.loading = true;
      this.loginFail = false

      this.login({email: this.email, password: this.password}).catch(() => {
        this.loginFail = true
      }).finally(() => {
        this.loading = false;
      })
    },
    handleReset() {
      this.loading = true;
      this.resetPassword(this.email).then((res) => {
        if (res.success) {
          this.resetSuccess = true
        }
      }).finally(() => {
        this.loading = false;
      })
    }
  }
}
</script>

<style lang="scss">
.login {
  //background-image: url("~@/assets/login_bg.jpg");
  background: #EFF2F9;

  .el-input {
    width: 306px;
    height: 38px !important;
    border: 1px solid rgba(109, 161, 208, 0.2);

    border-radius: 12px;
    background-color: white
  }

  .el-input__inner {
    border-radius: 12px;
    box-shadow: inset 0px 4px 8px rgba(109, 161, 208, 0.13);
  }
}
</style>

<style lang="scss" scoped>
.login {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  .logo-img {
    margin-right: 10px !important;
  }

  .err {
    text-align: center;
    font-size: 16px;
    font-weight: 700;
    color: $clr-green;
  }

  &-form {
    background-color: white;
    box-shadow: 0px 0px 24px rgba(109, 161, 208, 0.13);
    border-radius: 12px;
    //margin: auto 30px;
    width: 466px;
    height: 346px;
    padding: 40px 50px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 30px;

    .forget {
      color: #0EA9EE;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;

      &:hover {
        color: #0085FF;
      }

      svg {
        margin-right: 9px;
      }
    }

    .logo {

    }

    .info {
      font-size: 18px;
      color: #0EA9EE;
      text-align: center;
    }

    .inputs {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 20px;
    }

    #login, #instructions {
      width: 230px;
      height: 38px;

      transition: all ease 0.2s;

      font-size: 16px;
      font-weight: 500;

      background: #0EA9EE;
      border-radius: 99px;

      &.is-disabled {
        background: $clr-grey;

        &:hover {
          background: $clr-grey;
        }
      }

      &:hover {
        background: #0085FF;
      }
    }
  }
}
</style>
